.portfolio {
  padding-top: 40px;
  padding-bottom: 40px;
}

.portfolio__categories {
  flex-wrap: wrap;
  align-items: center;
  margin-bottom: 40px;
  display: flex;
}

.portfolio__tag:not(:last-child) {
  margin-right: 10px;
}

.portfolio__tag {
  cursor: pointer;
  color: #222021;
  font-size: 18px;
  font-weight: 500;
  position: relative;
}

.portfolio__tag.active.portfolio__tag:after {
  width: 100%;
}

.portfolio__tag:after {
  content: "";
  background-color: #222021;
  width: 0%;
  height: 1px;
  transition: width .3s ease-in-out;
  display: block;
  position: absolute;
  bottom: 0;
}

@media (hover: hover) {
  .portfolio__tag:hover.portfolio__tag:after {
    width: 100%;
  }
}

.portfolio__list {
  grid-template-columns: repeat(3, 1fr);
  gap: 20px;
  display: grid;
}

.portfolio__item--img {
  object-fit: cover;
  width: 362px;
  height: 362px;
  margin-bottom: 40px;
  transition: all 1s ease-out;
}

.portfolio__item {
  flex-direction: column;
  align-items: center;
  max-width: 362px;
  max-height: 362px;
  display: flex;
  position: relative;
  overflow: hidden;
}

@media (hover: hover) {
  .portfolio__item:hover .portfolio__item--img {
    opacity: 1;
    filter: brightness(65%);
    transform: scale(1.21);
  }

  .portfolio__item:hover .portfolio__item--name {
    opacity: 1;
    transform: translateY(-20px);
  }
}

.portfolio__item--link {
  width: 100%;
  height: 100%;
  position: absolute;
}

.portfolio__item--name {
  text-transform: uppercase;
  letter-spacing: 2px;
  text-align: center;
  color: #d6d7dd;
  -webkit-user-select: none;
  user-select: none;
  opacity: 0;
  margin: 0;
  font-size: 32px;
  font-weight: 500;
  transition: all 1s ease-in-out;
  position: absolute;
  bottom: 0;
}

@media (width <= 1200px) {
  .portfolio__list {
    grid-template-columns: repeat(2, 1fr);
    justify-items: center;
  }
}

@media (width <= 781px) {
  .portfolio__item--img {
    object-fit: cover;
    width: 300px;
    height: 300px;
    margin-bottom: 40px;
    transition: all 1s ease-out;
  }

  .portfolio__item {
    max-width: 300px;
    max-height: 300px;
  }
}

@media (width <= 662px) {
  .portfolio__list {
    grid-template-columns: repeat(1, 1fr);
    justify-items: center;
  }
}
